// Form Components
@mixin input {
	@include transition;
	@include breakpoint(phone) {
		margin: ($input-margin / 2) !important;
		padding: 5px 15px; }
	border: 0;
	border: 2px solid transparent;
	box-shadow: 0 0 5px transparentize($black, .7);
	font-size: 1em;
	margin: $input-margin;
	padding: 10px 35px;
	width: 100%;

	[class^='col-'] & {
		margin: $input-margin 0; } }


form {
	margin: 10px;
	padding: 20px 40px;
	@include breakpoint(tablet) {
		padding: 20px 10px;

		.row:not(.no-gutter) [class^='col-'] {
			padding: (($gutter / 2) / 2) 0; } }

	@include breakpoint(phone) {
		padding: 10px 0; } }

input {
	@include input;

	&:not([type='button']):not([type='submit']):not([type='reset']):not(.input-flat) {
		border-radius: $input-radius;
		height: 40px; }

	&[type='search'] {
		@include prefix(appearance, none, webkit, false);
		background: $white url('data:image/pngbase64,') no-repeat 10px;
		background-size: 24px; }

	&[type='radio'],
	&[type='checkbox'] {
		@include prefix(appearance, none, webkit, false);
		@include transition;
		box-shadow: none;
		height: $input-size !important;
		margin: 5px;
		padding: 2px;
		width: $input-size;

		&::before,
		&::after {
			@include transition;
			border-radius: 0;
			content: '';
			display: block;
			position: relative;
			transition-property: transform, border-color; }

		&:not(.input-flat)::before,
		&:not(.input-flat)::after {
			border-radius: 15px; }

		&::before {
			border: 2px solid $dark;
			height: $input-size;
			left: -10px;
			top: -6px;
			width: $input-size; }

		&::after {
			background: $dark;
			height: $input-size - 2;
			left: -7px;
			top: ($input-size + 7px) * -1;
			transform: scale(0) !important;
			width: $input-size - 2; }

		&:checked::after {
			transform: scale(.8) !important; }

		&:disabled {
			cursor: not-allowed; }

		&.input-light {
			&::before {
				border-color: $snow-white; }

			&::after {
				background-color: $snow-white; } }

		// input color
		@include themetize($class: input, $pseudos: (after: background-color, before: border-color));

		// outline input
		@include themetize($class: input-, $pseudos: ('checked::after': background-color, 'checked::before': border-color)); }

	&[type='date'],
	&[type='time'],
	&[type='datetime-local'],
	&[type='month'] {
		@include prefix(appearance, listbox, webkit, false); }

	&:not([type='radio']):not([type='checkbox']) {
		@include themetize(input, (properties: background-color, auto-color: true));
		@include themetize(input-, $hover-properties: (properties: border-bottom-color)) {
			box-shadow: none;
			border-bottom-color: transparentize($black, .85); } } }

textarea {
	@include input;
	height: auto !important;
	resize: none; }

fieldset {
	border: 0;
	margin: 0;
	min-width: 0;
	padding: 0; }
